/* global chrome:true */
export default class Storage {
    get = itemCata => {
      return JSON.parse(localStorage.getItem(itemCata));
    }
  
    set = (item, itemCata) => {
      do {
        item.pop();
      } while (item.length > 10)
      return localStorage.setItem(itemCata, JSON.stringify(item));
    }
  }
  